.recommended {
  &__title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 40px;
    }

    @include onDesktop {
      margin-bottom: 64px;
    }
  }

  &__item {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 40px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}
